import React from 'react';
import { Container } from 'react-bootstrap';
import Carousel from '../homeCarousel';

const popularProducts = () => (
    <div className="my-3" id="products">
        <Container className="p-0">
            <div className="px-2 pt-4 px-md-4 pt-md-3 bg-white shadow-sm rounded">
                <div className="d-flex mb-3 align-items-baseline border-bottom">
                    <h3 className="h5 fw-700 mb-0">
                        <span class="border-bottom border-primary border-width-2 pb-3 d-inline-block">Most Popular Products</span>
                    </h3>
                    <a href="https://grameenbajar.com/category/-" class="ml-auto mr-0 btn btn-primary btn-sm shadow-md">View More</a>
                </div>
                <Carousel />
            </div>
        </Container>
    </div>
);

export default popularProducts;
